import { Component, OnInit, Output, Input, OnChanges, EventEmitter } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from 'primeng/primeng';
import * as _ from 'lodash';
import { CoopProjectTypeListQueryBean, CoopProjectTypeSetBean, CoopProjectTypeDetailQueryBean, ProjectElementGroupDetailQueryBean } from '../bean/project-main-manage-search-other.bean';
import { ProjectHttpService } from '../../http/project-main-manage.service';


@Component({
    selector: "new-modification",
    templateUrl: './new-modification.component.html',
    styleUrls: ['./new-modification.component.scss'],
})

export class NewModification implements OnInit {
    @Input('stateType') stateType: any;
    @Input('msgss') msgss: any;
    @Output() data = new EventEmitter()
    pageSize: number;
    falge: boolean = false;
    first: any = 0;
    total: any;
    coopProjectTypeListQueryBean: CoopProjectTypeListQueryBean = new CoopProjectTypeListQueryBean;//合作项目类型列表查询
    coopProjectTypeSetBean: CoopProjectTypeSetBean = new CoopProjectTypeSetBean();//合作项目类型新增、修改、删除
    coopProjectTypeDetailQueryBean: CoopProjectTypeDetailQueryBean = new CoopProjectTypeDetailQueryBean();//合作项目类型详情查询
    typeTitle: any;
    display: boolean = false;
    dialogHead: any = '新增';
    dialogType: any;
    groupId: any = '';
    msgs: any;
    detailsDisplay: boolean = false;
    tableData: any[] = [];
    typeList: any[];
    type: any;
    upData: any[] = [];
    tableHead: any[] = [
        {
            label: '序号',
            value: 'tit'
        },
        {
            label: '要素名称',
            value: 'elementGroupName'
        },
        {
            label: '是否为组件',
            value: 'elementGroupFlag'
        },
        {
            label: '允许多条',
            value: 'multipleFlag'
        },
        {
            label: '要素标识',
            value: 'elementEnName'
        },
        {
            label: '是否必输',
            value: 'mustInput'
        },
        {
            label: '要素字段类型',
            value: 'elementType'
        },
        {
            label: '要素精度',
            value: 'elementPrecision'
        },
        {
            label: '要素检查服务名',
            value: 'frontElementCheckServe'
        },
        {
            label: '最小值',
            value: 'minValue'
        },
        {
            label: '最大值',
            value: 'maxValue'
        },
        {
            label: '缺省值',
            value: 'defValue'
        },
        {
            label: '列表值选项',
            value: 'listElement'
        },
    ];
    //详情
    DetailList: CoopProjectTypeSetBean = new CoopProjectTypeSetBean();
    projectElementGroupDetailQueryBean: ProjectElementGroupDetailQueryBean = new ProjectElementGroupDetailQueryBean();
    //码值
    code: any;
    coopPartnerType: any[];
    releProtocolType: any[];
    elementGroupFlag: any[];
    multipleFlag: any[];
    mustInput: any[];
    elementType: any[];
    constructor(
        public httpService: ProjectHttpService,
        public commfunc: Commfunc,
    ) {

    }
    ngOnInit() {
        this.coopProjectTypeListQueryBean.pageNum = 1;
        this.pageSize = 10;

        this.codeValues();
        //列表项取值
        this.coopPartnerType = this.code['coopPartnerType'];
        this.elementType = this.code['elementType'];
        this.elementGroupFlag = this.code['elementGroupFlag'];
        this.multipleFlag = this.code['multipleFlag'];
        this.mustInput = this.code['mustInput'];
        this.releProtocolType = this.code['releProtocolType'];

        //修改
        if (this.msgss && this.msgss != '') {
            this.detail(this.msgss)
        } else {
            this.coopProjectTypeSetBean.operateType = '1';
        }
    }
    //修改数据
    updataList(data) {
        this.coopProjectTypeSetBean = new CoopProjectTypeSetBean();
        this.coopProjectTypeSetBean.operateType = '2';
        this.coopProjectTypeSetBean.coopPartnerType = data['coopPartnerType'];
        this.coopProjectTypeSetBean.coopProjectTypeDesc = data['coopProjectTypeDesc'];
        this.coopProjectTypeSetBean.elementGroupId = data['elementGroupId'];
        this.coopProjectTypeSetBean.releProtocolType = data['releProtocolType'];
        this.coopProjectTypeSetBean.coopProjectTypeName = data['coopProjectTypeName'];
        this.coopProjectTypeSetBean.coopProjectTypeId = data['coopProjectTypeId'];
        this.groupId = data['elementGroupId'];
        this.coopProjectTypeSetBean.processId = data['processId'];
    }
    //接收组件传值
    close(e) {
        this.coopProjectTypeSetBean.elementGroupId = e
    }
    //获取详情信息
    detail(msg) {
        console.log(msg)
        this.coopProjectTypeDetailQueryBean.coopProjectTypeId = msg['coopProjectTypeId']
        // this.coopProjectTypeDetailQueryBean.processId = msg['processId'];

        this.httpService.coopProjectTypeDetailQuery(this.coopProjectTypeDetailQueryBean).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.DetailList = data
                this.DetailList = _.clone(this.DetailList)
                this.updataList(data)
                this.getEleList(data);
                // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }
    //获取元素列表
    getEleList(data) {
        this.projectElementGroupDetailQueryBean.elementGroupId = data.elementGroupId
        this.httpService.projectElementGroupDetailQuery(this.projectElementGroupDetailQueryBean).subscribe(data => {
            if (data.returnCode.code == "success") {
                let listDataT = _.clone(data['elementsList'])
                listDataT.forEach(e => {
                    let list = '';
                    e.listElement.forEach(et => {
                        list += et.optionKey + '_' + et.optionValue;
                    })
                    e.listElement = list;
                })
                this.upData = listDataT
                // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }
    //保存
    save() {
        this.msgs = [];
        if (this.coopProjectTypeSetBean.coopProjectTypeName == null || this.coopProjectTypeSetBean.coopProjectTypeName == undefined) {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '合作项目类型名称不能为空' });
            return;
        }
        else if (this.coopProjectTypeSetBean.coopPartnerType == null || this.coopProjectTypeSetBean.coopPartnerType == undefined) {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '合作类型不能为空' });
            return;
        } else if (this.coopProjectTypeSetBean.elementGroupId == null || this.coopProjectTypeSetBean.elementGroupId == undefined) {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择项目主体要素组合' });
            return;
        } else if (this.coopProjectTypeSetBean.processId == null || this.coopProjectTypeSetBean.processId == undefined) {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '流程编号不能为空' });
            return;
        }

        this.httpService.coopProjectTypeSet(this.coopProjectTypeSetBean).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                setTimeout(() => {
                    this.data.emit()
                }, 1500)
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })

    }
    //获取码值
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

}