import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from 'primeng/primeng';
import * as _ from 'lodash';
import { ProjectHttpService } from '../http/project-main-manage.service';
import { CoopProjectTypeListQueryBean, CoopProjectTypeSetBean, CoopProjectTypeDetailQueryBean, ProjectElementGroupDetailQueryBean } from './bean/project-main-manage-search-other.bean';


@Component({
    selector: "project-main-manage-search-other",
    templateUrl: './project-main-manage-search-other.component.html',
    styleUrls: ['./project-main-manage-search-other.component.scss'],
})

export class ProjectMainManageSearchOther implements OnInit {
    pageSize: number;
    first: any = 0;
    total: any;
    dataMsg: any;
    coopProjectTypeListQueryBean: CoopProjectTypeListQueryBean = new CoopProjectTypeListQueryBean;//合作项目类型列表查询
    coopProjectTypeSetBean: CoopProjectTypeSetBean = new CoopProjectTypeSetBean();//合作项目类型新增、修改、删除
    coopProjectTypeDetailQueryBean: CoopProjectTypeDetailQueryBean = new CoopProjectTypeDetailQueryBean();//合作项目类型详情查询
    typeTitle: any;
    display: boolean = false;
    dialogHead: any = '新增';
    dialogType: any;
    groupId: any = '';
    msgs: any;
    detailsDisplay: boolean = false;
    tableData: any[] = [];
    typeList: any[];
    type: any;
    upData: any[] = [];
    tableHead: any[] = [
        {
            label: '序号',
            value: 'tit'
        },
        {
            label: '要素名称',
            value: 'elementGroupName'
        },
        {
            label: '是否为组件',
            value: 'elementGroupFlag'
        },
        {
            label: '允许多条',
            value: 'multipleFlag'
        },
        {
            label: '要素标识',
            value: 'elementEnName'
        },
        {
            label: '是否必输',
            value: 'mustInput'
        },
        {
            label: '要素字段类型',
            value: 'elementType'
        },
        {
            label: '要素精度',
            value: 'elementPrecision'
        },
        {
            label: '要素检查服务名',
            value: 'frontElementCheckServe'
        },
        {
            label: '最小值',
            value: 'minValue'
        },
        {
            label: '最大值',
            value: 'maxValue'
        },
        {
            label: '缺省值',
            value: 'defValue'
        },
        {
            label: '列表值选项',
            value: 'listElement'
        },
    ];
    //详情
    DetailList: CoopProjectTypeSetBean = new CoopProjectTypeSetBean();
    projectElementGroupDetailQueryBean: ProjectElementGroupDetailQueryBean = new ProjectElementGroupDetailQueryBean();
    //码值
    code: any;
    coopPartnerType: any[];
    // needPlan: any[];
    // planNeedApprove: any[];
    // projectBodyNeedApprove: any[];
    // projectBodyHaveLimit: any[];
    releProtocolType: any[];
    // buildCrowdConfig: any[];
    elementGroupFlag: any[];
    multipleFlag: any[];
    mustInput: any[];
    elementType: any[];
    constructor(
        public httpService: ProjectHttpService,
        public commfunc: Commfunc,
    ) {

    }
    ngOnInit() {
        this.coopProjectTypeListQueryBean.pageNum = 1;
        this.coopProjectTypeListQueryBean.pageSize = 10;
        this.query();
        this.codeValues();

        //列表项取值
        this.coopPartnerType = this.code['coopPartnerType'];
        // this.needPlan = this.code['needPlan'];
        // this.planNeedApprove = this.code['planNeedApprove'];
        // this.projectBodyNeedApprove = this.code['projectBodyNeedApprove'];
        // this.projectBodyHaveLimit = this.code['projectBodyHaveLimit'];
        this.releProtocolType = this.code['releProtocolType'];
        // this.buildCrowdConfig = this.code['buildCrowdConfig'];
        this.elementType = this.code['elementType'];
        this.elementGroupFlag = this.code['elementGroupFlag'];
        this.multipleFlag = this.code['multipleFlag'];
        this.mustInput = this.code['mustInput'];
    }
    closeMsg() {
        this.display = false;
        this.query();
        this.dataMsg = '';

    }
    clear() {
        this.dataMsg = '';
    }
    clearQuery() {
        this.coopProjectTypeListQueryBean.pageSize = 10;
        this.coopProjectTypeListQueryBean.pageNum = 1;
        this.query();
    }
    // 重置
    reset() {
        this.coopProjectTypeListQueryBean.pageSize = 10;
        this.coopProjectTypeListQueryBean.pageNum = 0;
        this.coopProjectTypeListQueryBean.coopProjectTypeName = '';
        this.coopProjectTypeListQueryBean.coopProjectTypeId = '';
        this.query();
    }
    //列表查询
    query() {
        this.msgs = [];
        
        this.httpService.coopProjectTypeListQuery(this.coopProjectTypeListQueryBean).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.typeList = data['returnList']
                this.total = data.total;
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }
    //新建按钮点击
    newHid() {
        this.display = true;
        this.dialogHead = '新建';
        this.dialogType = '01';
        this.coopProjectTypeSetBean = new CoopProjectTypeSetBean();
        this.coopProjectTypeSetBean.operateType = '1';
        this.groupId = '';
    }
    //修改按钮点击
    upDate(i) {
        this.display = true;
        this.dialogHead = '修改';
        this.dialogType = '02';
        this.dataMsg = i;
    }
    show(i) {
        this.detailsDisplay = true;
        this.dataMsg = i;
    }
    //接收组件传值
    close(e) {
        this.coopProjectTypeSetBean.elementGroupId = e
    }
    //删除
    delete(i) {
        this.coopProjectTypeSetBean = new CoopProjectTypeSetBean();
        this.coopProjectTypeSetBean.operateType = '3';
        this.coopProjectTypeSetBean.coopProjectTypeId = i['coopProjectTypeId'];
        this.httpService.coopProjectTypeSet(this.coopProjectTypeSetBean).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.query();
                // this.msgs.push({ severity: 'success', summary: '提示', detail: '删除成功' });
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });

            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });

        })

    }
    //获取码值
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //分页
    paginate(event) {
        console.log(event)
        this.coopProjectTypeListQueryBean.pageSize = event.rows * 1;
        //当前页
        let currentPage = event.page + 1;
        this.coopProjectTypeListQueryBean.pageNum = currentPage;
        this.first = event.page * event.rows;
        /**
         * --2018.1.15
         */
        //调用查询的方法
        this.query();
    }

    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }

}